import { mapActions, mapGetters } from "vuex";

export default {
  data() {
    return {
      post: this.searchPost(this.postName),
    };
  },
  methods: {
    ...mapGetters("posts", ["getPosts"]),
    searchPost(title) {
      return this.getPosts().find((post) => post.slug == title);
    },
  },
  props: ["postName"],
  head() {
    return {
      title: this.post.title + " | Adam Rashid",
    };
  },
};
